import React from 'react'
class Form extends React.Component {
    constructor(props) {
        super(props);

        this.state = {firstName : '', isGoing: false, fruit : 'mangoes'};
        this.handleOnChange = this.handleOnChange.bind(this);
        this.handleOnSubmit = this.handleOnSubmit.bind(this);
        this.handleOnChangeCheckbox = this.handleOnChangeCheckbox.bind(this);

    }

    handleOnChange(event)
    {
        debugger
        const name = event.target.name;
        const value = event.target.name === 'isGoing' ? event.target.checked : event.target.value; 
        
        //Here [name] is computed property. 
        this.setState({[name] : value});
        
    }

    handleOnChangeCheckbox(event){
        debugger
        this.setState({isGoing : event.target.checked})
    }

    handleOnSubmit(event){
        
          alert("Form submitted with "+JSON.stringify(this.state))
          event.preventDefault();
    }

    render() {
        return (
            <div>
                <form onSubmit={this.handleOnSubmit}>
                <label>
                    Select your fav fruit
                    <select onChange={this.handleOnChange} value={this.state.value}>
                        <option value='manogoes'>mangoes</option>
                        <option value='grapes'>grapes</option>
                    </select>
                </label>
                <label>
                    Are you going :
                    <input name="isGoing" type="checkbox" value={this.state.isGoing} onChange={this.handleOnChange}/>
                </label>
                <input name='firstName' value={this.state.firstName} onChange={this.handleOnChange}/>
                <input type="submit"/>
                </form>
            </div>
        );
    }
}

Form.propTypes = {

};

export default Form;


