import React, { useState } from "react";

import {
  useHistory,
  BrowserRouter as Router,
  Switch,
  Route,
  NavLink,
  Redirect,
} from "react-router-dom";
import "./UseHistoryPractice.css";
function Contact() {
  debugger;
  // var history = useHistory();
  const [route, RouteHandler] = useState("");
  var history = useHistory();

  function valueOnChange(event) {
    debugger;
    const value = event.target.value;
    RouteHandler(value);
  }

  function valueOnSubmit(event) {
    debugger;
    event.preventDefault();
    history.push(`/${route}`);
  }

  return (
    <div>
      <form onSubmit={(e) => valueOnSubmit(e)}>
        <input
          type="text"
          placeholder="Enter the Route"
          onChange={(e) => valueOnChange(e)}
        ></input>
        <input type="submit"></input>
      </form>
    </div>
  );
}

function About() {
  return <div>Welcome to About route</div>;
}

function Home() {
  return <div>Welcome to Home route</div>;
}

function User() {
  return <div>Welcome to User route</div>;
}

function Error() {
  return (
    <div>
      <h1>404 not found</h1>
    </div>
  );
}
function App() {
  debugger;
  return (
    <div className="p-5">
      <Router>
        <div className="navBackground">
          <header className="d-flex justify-content-center">
            <nav className=" w-50 d-flex justify-content-between py-3">
              <NavLink to="/search">Search</NavLink>
              <NavLink to="/about">About</NavLink>
              <NavLink to="/home">Home</NavLink>
              <NavLink to="/user">User</NavLink>
            </nav>
          </header>
          <div className="d-flex justify-content-center py-4">
            <Switch>
              <Route exact path="/search">
                <Contact />
              </Route>
              <Route path="/about">
                <About />
              </Route>
              <Route path="/home">
                <Home />
              </Route>
              <Route path="/user">
                <User />
              </Route>
              <Route path="*">
                <Error />
              </Route>
              {/* <Redirect to='/' /><Error/> */}
            </Switch>
          </div>
        </div>
      </Router>
    </div>
  );
}

export default App;
